.Input {
    width: 350px;
    height: 35px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 15px;
}

.Input:hover {
    background-color: #5cdb95;
}

.InputLabel {
    left: 0;
    position: absolute;
    margin-left: 10px;
}

.InputElement {
    background-color: aqua;
    position: absolute;
    right: 0;
    margin-right: 10px;
}
