.SendMessageFormContainer {
    position: absolute;
    height: 89%;
    width: 100%;
    top: 75px;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    overflow: scroll;
}

.SendMessageFormContainer form {
    width: 100%;
    margin: 15px;
}

.SendMessageFormContainer select {
    width: 99%;
}

.SendMessageFormContainer input {
    padding: 3px 5px;
    width: 95%;
}

.SendMessageFormContainer textarea {
    padding: 3px 5px;
    width: 95%;
    min-height: 150px;
}
