.PropertyContainer {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 5px;
}

.PropertyField {
    display: flex;
    width: 100%;
    justify-content: space-between;
}

.PropertyField input {
    margin-right: 2px;
    width: 75% !important;
}

.PropertyField label {
    padding-right: 3px;
    padding-top: 2px;
}
