.QueuesContainer {
    position: absolute;
    height: 85%;
    width: 90%;
    top: 75px;
}

.Queues {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    height: 93%;
    width: 100%;
    overflow: scroll;
    white-space: nowrap;
}

.QueueSearch {
    top: 11%;
    left: 0;
    width: 100%;
    height: 23px;
    min-height: 23px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.Footer {
    width: 100%;
    height: 5%;
    min-height: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 10px;
}
