.Title {
    font-weight: bold;
    font-size: 1rem;
    margin: 0;
    padding: 20px 0;
    width: 100%;
    height: 30px;
    background-color: #05386b;
    text-align: center;
    top: 0;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
}

.TitleTextWrapper {
    width: 85%;
}

.NoWrap {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
