.Widget {
    background-color: #379683;
    width: 30%;
    height: 80%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    overflow: hidden;
    border-radius: 15px 50px;
}

.Hidden {
    background-color: transparent;
}
